@import "../../../css-config/Mixin.scss";
.Product-card {
  // width: calc(50% -5px);
  width: 50%;
  margin-bottom: 20px;
  @include md {
    // width: calc(25% -15px);
    width: 23%;
  }
  .thumbnail {
    width: 100%;
    height: 180px;
    background-color: rgba(0, 0, 0, 0.05);
    margin-bottom: 10px;
    padding: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include md {
      height: 350px;
    }
    img {
      transition: all ease 0.3s;
      display: block;
      width: 50%;
      @include md {
        width: 80%;
      }
    }
  }
  .Prod-details {
    .name {
      font-size: 14px;
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      @include md {
        font-size: 16px;
        margin-bottom: 10px;
      }
    }
    .price {
      font-size: 18px;
      @include md {
        font-size: 24px;
      }
    }
  }
  &:hover{
    .thumbnail{
        img{
            transform: scale(1.2);
        }
    }
  }
}
